*,
*::before,
*::after {
  box-sizing: border-box;
}

:root {
  scroll-padding-top: 3.75rem;
}
@media (prefers-reduced-motion: no-preference) {
  :root {
    scroll-behavior: smooth;
  }
}
@media (min-width: 1024px) {
  :root {
    scroll-padding-top: 7.5rem;
  }
}

body {
  position: relative;
  margin: 0;
  font-family: var(--bs-body-font-family);
  font-synthesis: none;
  font-size: var(--bs-body-font-size);
  font-weight: var(--bs-body-font-weight);
  line-height: var(--bs-body-line-height);
  text-align: var(--bs-body-text-align);
  /* rtl:remove */
  letter-spacing: -0.00625rem;
  background-color: var(--bs-body-bg);
  -webkit-text-size-adjust: 100%;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-font-smoothing: subpixel-antialiased;
  text-rendering: optimizespeed;
}

:focus {
  z-index: 5;
  isolation: isolate;
  outline: 3px solid var(--bs-focus-visible-outer-color);
  outline-offset: 2px;
  box-shadow: 0 0 0 2px var(--bs-focus-visible-inner-color);
}

.js-focus-visible :focus:not([data-focus-visible-added]):not(.focus-ring):not(.form-select:invalid):not(.form-control[type=file]:invalid),
.js-focus-visible .focus:not([data-focus-visible-added]):not(.focus-ring):not(.form-select:invalid):not(.form-control[type=file]:invalid) {
  outline: 0 !important;
  box-shadow: none;
}

:focus:not(:focus-visible):not(.focus-ring):not(.form-select:invalid):not(.form-control[type=file]:invalid) {
  outline: 0 !important;
  box-shadow: none;
}

hr {
  margin: 1.25rem 0;
  color: inherit;
  border: 0;
  border-top: var(--bs-border-width) solid;
}

h4,
h5,
h6, h2,
h3, h1 {
  margin-top: 0;
  margin-bottom: 1.25rem;
  font-size: 1rem;
  font-weight: 700;
  line-height: 1.125;
  color: var(--bs-heading-color);
  /* rtl:remove */
  letter-spacing: -0.00625rem;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizelegibility;
}

h1 {
  font-size: 1.25rem;
  line-height: 1.1;
  /* rtl:remove */
  letter-spacing: -0.025rem;
}

h2,
h3 {
  font-size: 1.125rem;
  line-height: 1.1111111111;
  /* rtl:remove */
  letter-spacing: -0.0125rem;
}

p {
  margin-top: 0;
  margin-bottom: 1rem;
}

abbr[title] {
  text-decoration: underline dotted;
  cursor: help;
  text-decoration-skip-ink: none;
}

address {
  margin-bottom: 1rem;
  font-style: normal;
  line-height: inherit;
}

ol,
ul {
  padding-left: 2rem;
}

ol,
ul,
dl {
  margin-top: 0;
  margin-bottom: 1rem;
}

ol ol,
ul ul,
ol ul,
ul ol {
  margin-bottom: 0;
}

ul {
  list-style-type: square;
}

li::marker {
  color: var(--bs-primary);
  vertical-align: middle;
}
ol li::marker {
  color: inherit;
}

li li::marker {
  color: var(--bs-secondary-color);
}

li li li::marker {
  color: var(--bs-tertiary-color);
}

li::before {
  color: var(--bs-primary);
  vertical-align: text-top;
}
ol li::before {
  color: inherit;
}

li li::before {
  color: var(--bs-secondary-color);
}

li li li::before {
  color: var(--bs-tertiary-color);
}

dt {
  font-weight: 700;
}

dd {
  margin-bottom: 0.5rem;
  margin-left: 0;
}

blockquote {
  margin: 0 0 1rem;
}

b,
em,
strong {
  font-weight: 700;
}

small {
  font-size: 0.875rem;
  font-weight: 400;
  line-height: 1.1428571429;
}

mark {
  padding: 0 0.1875em;
  color: var(--bs-highlight-color);
  background-color: var(--bs-highlight-bg);
}

sub,
sup {
  position: relative;
  font-size: 0.75em;
  line-height: 0;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

a {
  color: rgba(var(--bs-link-color-rgb), var(--bs-link-opacity, 1));
  text-decoration: underline;
}
a:hover {
  --bs-link-color-rgb: var(--bs-link-hover-color-rgb);
}

a:not([href]):not([class]), a:not([href]):not([class]):hover {
  color: inherit;
  text-decoration: none;
}

var,
pre,
code,
kbd,
samp {
  font-family: var(--bs-font-monospace);
  font-size: 1em;
}

pre {
  display: block;
  margin-top: 0;
  margin-bottom: 1rem;
  overflow: auto;
  font-size: 0.875em;
  line-height: 1.25;
  color: var(--bs-code-color);
}
pre code {
  font-size: inherit;
  color: inherit;
  word-break: normal;
}

var,
code {
  font-size: 0.875em;
  font-style: normal;
  line-height: 1.1428571429;
  color: var(--bs-code-color);
  word-wrap: break-word;
}
a > var,
a > code {
  color: inherit;
}

kbd {
  padding: 0.0625rem 0.0625rem;
  font-size: 0.875em;
  color: var(--bs-kbd-color, #000);
  background-color: var(--bs-kbd-bg, #eee);
}
kbd kbd {
  padding: 0;
  font-size: 1em;
}

figure {
  margin: 0 0 1rem;
}

img,
svg {
  vertical-align: middle;
}

table {
  font-feature-settings: "tnum";
  font-variant-numeric: tabular-nums;
  caption-side: top;
  border-collapse: collapse;
}

caption {
  padding-top: 0.75rem;
  padding-bottom: 0.75rem;
